const app = getApp().globalData;
const { navBar } = app;
export default {
	data() {
		return {
			navBar: navBar,
			cashList: [
				{
					name: '正常',
					id: '2'
				},
				{
					name: '未开始',
					id: '1'
				},
				{
					name: '已使用',
					id: '3'
				},
				{
					name: '已过期',
					id: '4'
				}
			],
			activeTab: 0,
			price: '1200.00'
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		detailPage(){
			uni.navigateTo({
				url:'cashdetail'
			})
		}
	}
};
